import React, { Component } from 'react'
import MainNav from "../Navigation/MainNav";
import Inputs from "../common/InputFields";
import { Link } from "react-router-dom";
import { InputValidate } from "../../redux/actions/Inputs";
export default class screen2 extends Component {
    constructor(props) {
        super(props);
        this.state = {
            email: null,
            errors: {
                email: null,
            },
        };
    }
    setStateHandler = val => {
        this.setState({
            errors: val.errors,
            [val.name]: val[val.name]
        })
    }
    HandleChanging = e => {
        let errors = InputValidate(e, this.state);
        this.setState(errors);
    }
    render() {
        return (
            <React.Fragment>
                <div className="container-fluid" id="Forget-2">
                    <img src="./img/logo.png" alt="" />

                    <div className="row">
                        <div className="col-12">
                            <span>Password Reset</span>
                            <hr />
                            <p>Your password will be reset so you can get back to watching.</p>
                            <Inputs label="E-mail" name="email" types="email" state={this.state} stateHandler={this.setStateHandler} />
                            {this.state.errors.email !== "" ? <span className="error text-danger">{this.state.errors.email}</span> : ""}
                            <Link to="/forget-password">Submit</Link>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}
